@charset "UTF-8";
/* Reset */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul, ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  font-family: inherit;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
}

@font-face {
  font-family: "Sora-Regular";
  src: url("../../src/assets/fonts/Sora-Regular.ttf");
}
@font-face {
  font-family: "Sora-Bold";
  src: url("../../src/assets/fonts/Sora-Bold.ttf");
}
@font-face {
  font-family: "Sora-ExtraBold";
  src: url("../../src/assets/fonts/Sora-ExtraBold.ttf");
  font-weight: 800;
}
@font-face {
  font-family: "Sora-SemiBold";
  src: url("../../src/assets/fonts/Sora-SemiBold.ttf");
}
body {
  font-family: "Sora-Regular", sans-serif;
  font-size: 16px;
  color: #71717A;
}

@media screen and (max-width: 1320px) {
  .container {
    margin: 0 20px;
  }
  .skills .skills_flip-card {
    justify-content: center;
  }
}
@media screen and (max-width: 1156px) {
  .hero .hero__content .hero__content-greeting {
    max-width: 400px;
  }
  .hero .hero__content .hero__content-description {
    max-width: 400px;
    margin-top: 80px;
  }
  .hero .hero__banner {
    top: 140px;
  }
  .hero .svg-size {
    width: 600px;
    height: auto;
  }
}
@media screen and (max-width: 860px) {
  .hero .hero__content {
    max-width: 349px;
    margin-top: 0;
  }
  .hero .hero__content .hero__content-description {
    margin-top: 20px;
  }
  .hero .hero__content .social_icon {
    margin-top: 30px;
  }
  .hero .hero__banner {
    top: 170px;
  }
  .hero .svg-size {
    width: 600px;
    height: auto;
  }
  .about_me .about_me__content {
    flex-direction: column-reverse;
  }
  .about_me .about_me__content .about_me__image {
    margin: 0 0 30px 0;
  }
  .my_project {
    padding-bottom: 30px;
  }
  .my_project .my_project__content {
    flex-direction: column;
    margin-bottom: 80px;
  }
  .contact .contact__description .contact__description-title {
    font-family: "Sora-Bold", sans-serif;
    font-size: 32px;
    font-weight: 400;
  }
  .contact .contact__description .contact__description-email {
    font-family: "Sora-SemiBold", sans-serif;
    font-size: 20px;
    font-weight: 600;
  }
}
/* Медиа-запрос для мобильных устройств */
@media (max-width: 750px) {
  .hero {
    flex-direction: column-reverse;
  }
  .hero .hero__content {
    margin: 0 auto;
  }
  .hero .hero__content .hero__content-greeting {
    margin-top: 68px;
  }
  .hero .hero__content .hero__content-title {
    font-size: 28px;
  }
  .hero .hero__content .hero__content-description {
    margin-top: 20px;
  }
  .hero .hero__content .social_icon {
    margin-top: 30px;
  }
  .hero .hero__banner {
    position: static;
    margin: 0 auto;
  }
  .hero .svg-size {
    width: 400px;
    height: auto;
  }
  .my_project .my_project__content .my_project__image .project__image {
    max-width: 100%;
    height: 100%;
  }
  .my_project .my_project__content .my_project__description {
    max-width: 100%;
  }
  .contact {
    flex-direction: column-reverse;
    gap: 60px;
  }
  .contact .contact__form {
    width: 100%;
  }
  .contact .contact__form .form-details .form-group {
    width: 100%;
  }
  .contact .contact__form .form-details .form-group-textarea {
    width: 100%;
  }
  .contact .contact__form .form-details .btn_icon-bar {
    flex-direction: column;
    gap: 20px;
  }
  .contact .contact__description {
    width: 100%;
  }
}
@media (max-width: 490px) {
  .about_me .about_me__content .about_me__image .about_me__flip-card {
    width: 280px;
    height: 280px;
  }
  .my_project {
    padding: 40px 0;
  }
  .my_project .my_project__title {
    font-size: 28px;
    margin-bottom: 40px;
  }
  .my_project .my_project__content .my_project__image {
    height: 500px;
    width: 100%;
    overflow: hidden;
    display: flex;
    justify-content: center;
  }
  .my_project .my_project__content .my_project__image .project__image {
    height: 100%;
    min-width: 100%;
    object-fit: cover;
  }
  .my_project .my_project__content .my_project__description .my_project__description-title {
    font-size: 20px;
  }
  .footer .footer__items {
    flex-direction: column;
  }
}
@media (max-width: 440px) {
  .hero {
    padding: 40px 0;
  }
  .hero .hero__content .hero__content-greeting {
    margin-top: 28px;
  }
  .hero .svg-size {
    width: 298px;
    height: auto;
  }
  .skills {
    padding: 10px 0 40px 0;
  }
  .skills .skills__title {
    margin-bottom: 40px;
  }
  .skills .skills__title .skills__title-text-first {
    font-size: 28px;
  }
  .skills .skills__title .skills__title-text-second {
    font-size: 28px;
  }
  .skills .skills_flip-card {
    gap: 20px;
  }
  .skills .flip-card {
    width: 130px;
    height: 130px;
  }
  .skills .flip-card img {
    width: 130px;
    height: auto;
  }
  .about_me {
    padding: 10px 0 40px 0;
  }
  .about_me .about_me__title .about_me__title-text-first {
    font-size: 28px;
  }
  .about_me .about_me__title .about_me__title-text-second {
    font-size: 28px;
  }
  .about_me .about_me__content .about_me__info {
    max-width: 330px;
  }
}